import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
// import vorfooter from "../components/vorfooter";

Vue.use(VueRouter);

const routes = [
  {
    path: "",
    name: "Home",
    component: Home
  },
  // {
  //   path: "/",
  //   name: "VorFooter",
  //   component: vorfooter
  // },

];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
